.bookcase__title{
font-weight: 700;
font-size: 32px;
line-height: 1.1875;
letter-spacing: -0.04em;

color: var(--primary-black);

@media screen and (min-width: 768px)
{
    font-size: 48px;
    line-height: 1.08333;
}

}
.book-card__link {
    display: inline-block;
  }

.bookcase__filter{
    color: var(--accent-color-blue);
};

.book-card__list {
    grid-template-rows: fit-content(1%);
    grid-template-columns: 1fr;
    grid-auto-rows: auto;
    row-gap: 40px;
    margin-bottom: 8px;
    display: grid;

    @media screen and (min-width: 768px) {
        grid-template-columns: repeat(3,1fr);
        column-gap: 25px;
        margin-bottom: 20px;
    }

    @media screen and (min-width: 1440px) {
    grid-template-columns: repeat(5,1fr);
    column-gap: 25px;
    margin-bottom: 32px;
}};

.bookshelf__title__category{
    letter-spacing: .03em;
    text-transform: uppercase;
    color: var(--category-and-autor-color);
    margin-bottom: 18px;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.28571;
}

.book-card__image {
    width: 335px;

 @media screen and (min-width: 768px) {
    height: 316px;
};
@media screen and (min-width: 1440px) {
    height: 256px;
};
}


.book__card__item{
  &:not(:last-child) {
    margin-bottom: 40px;
  }
}

